<!-- 添加学生 -->
<template>
  
  <section class="add">
    <div style="margin-left: 150px;">
    <el-tabs type="border-card" style="width: 1000px;background-color: #eee;height: 500px;">
  <el-tab-pane label="单次添加">
    <el-form ref="form" :model="form" label-width="80px" :inline="true"  :rules="rules2" >
      <el-form-item label="学号">
        <el-input v-model="form.studentId"></el-input>
      </el-form-item>
      <el-form-item label="姓名">
        <el-input v-model="form.studentName"></el-input>
      </el-form-item>
      <el-form-item label="性别">
        <el-input v-model="form.sex"></el-input>
      </el-form-item>
      <el-form-item label="学院">
        <el-input v-model="form.institute"></el-input>
      </el-form-item>
      <el-form-item label="所属专业">
        <el-input v-model="form.major" style="width: 300px;"></el-input>
      </el-form-item>
      <el-form-item label="年级">
        <el-input v-model="form.grade"></el-input>
      </el-form-item>
      <el-form-item label="班级">
        <el-input v-model="form.clazz"></el-input>
      </el-form-item>
      <el-form-item label="电话号码">
        <el-input v-model="form.tel"></el-input>
      </el-form-item>
      <el-form-item label="身份证号">
        <el-input v-model="form.cardId"></el-input>
      </el-form-item>
      <el-form-item label="邮箱">
        <el-input v-model="form.email"></el-input>
      </el-form-item>
      <!-- <el-form-item label="密码">
        <el-input v-model="form.pwd"></el-input>
      </el-form-item> -->
      <el-form-item label="头像">
          <el-upload
            class="avatar-uploader"
            action="/otherApi/upload"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl"
               :src="imageUrl"
               class="avatar">
          <i v-else
             class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
      </el-form-item>
      <el-form-item style="margin-left: 380px">
        <el-button type="primary" @click="onSubmit()" >立即创建</el-button>
        <el-button type="text" @click="cancel()" >取消</el-button>
      </el-form-item>
    </el-form>
  </el-tab-pane>
  
  <el-tab-pane label="批量添加">
    <div style="margin-bottom: 10px;float: left;margin-left: 300px;padding-top: 10px;"><span style="color: red;">*</span>注：需要按照模板进行导入</div>
      <el-button type="primary" style="margin-left: 50px;margin-bottom: 30px;" @click="downloadDemo" :loading="loadingDemo" icon="el-icon-download" >下载模板</el-button>
          <el-upload
          ref="upload"
            class="upload-demo"
            :action="act"
            drag
            multiple 
            :headers="headers"
            :on-success="handleSuccess1"
            :before-upload="beforeUpload"
            style="text-align: center;">
            <i class="el-icon-upload" style="font-size: 150px;margin-top: 60px;"></i>
            <div class="el-upload__text" style="font-size: 20px;margin-top: 20px;">将文件拖到此处，或<em>点击上传</em></div>
          </el-upload>
  </el-tab-pane>

</el-tabs>
</div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      form: { //表单数据初始化
        studentName: null,
        grade: null,
        major: null,
        clazz: null,
        institute: null,
        tel: null,
        email: null,
        cardId: null,
        sex: null,
        role: 2
      },
      ruleForm2: {
         
      },
      imageUrl: '',
      loadingDemo:false,
      act:'/otherApi/importStudent',
      headers: 
      {
        "Content-Type": "multipart/form-data;charset=UTF-8",
        
      },
      headers:{
        token:sessionStorage.getItem("token")
      }
    };
  },
  methods: {
    downloadDemo(){
      
      setTimeout(() => {
        console.log('已经下载了2秒');
        const link = document.createElement("a"); // 创建a标签
        var url="https://exam-xr.oss-cn-hangzhou.aliyuncs.com/online_exam/2024/01/20/%E5%AF%BC%E5%85%A5%E5%AD%A6%E7%94%9F%E6%A8%A1%E6%9D%BF.xlsx"
        link.href = url;
        link.download ="导入学生模板"+".xlsx"; // 重命名文件
        link.click();
        URL.revokeObjectURL(url);
        this.$message({
          type:'success',
          message:'下载成功'
        })
        this.loadingDemo=false
      
      }, 1000);
      this.loadingDemo=true

    },
    beforeUpload(file){
      var str=String(file.name)
      if(str.substring(str.lastIndexOf(".")+1,str.length)=="xlsx"||str.substring(str.lastIndexOf(".")+1,str.length)=="xls")
        return true
      else{
        this.$message({
          message:"文件类型不匹配，建议检查文件",
          type:'error'
        })
        return false;
      }
    },
    handleSuccess1(response, file, fileList) {
      // 提交保存成功后清空已上传的文件
      if(response.code==400){
        this.$message({
          message:"导入学生成功",
          type:'success'
        })
       
      }
      if(response.code==200){
        this.$message({
          message:response.message,
          type:'error'
        })
        this.$refs.upload.clearFiles();
      }
    },


    handleAvatarSuccess (res, file) {
      this.imageUrl = res.data;
    },
    beforeAvatarUpload (file) {

      const isLt2M = file.size / 1024 / 1024 <2;
            // const isJPG = file.type === 'image/png';
      // if(!isJPG){
      //   this.$message.error('上传头像图片只能是 png 格式!');
      // }
      if(!isLt2M){
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isLt2M;
    },
    onSubmit() { //数据提交
      this.$axios({
        url: '/peopleApi/student',
        method: 'post',
        data: {
          studentName: this.form.studentName,
          grade: this.form.grade,
          major: this.form.major,
          clazz: this.form.clazz,
          institute: this.form.institute,
          tel: this.form.tel,
          email: this.form.email,
          pwd: '123456',
          cardId: this.form.cardId,
          sex: this.form.sex,
          role: 2,
          avatar:this.imageUrl
        }
      }).then(res => {
        if(res.data.code == 200) {
          this.$message({
            message: '数据添加成功',
            type: 'success'
          })
          this.$router.push({path: '/studentManage'})
        }
      })
    },
    cancel() { //取消按钮
      this.form = {}
    },
    
  }
};
</script>
<style lang="less" scoped>

/deep/ .upload-demo .el-upload{
  width: 100%;
  
}
/deep/ .upload-demo .el-upload-dragger{
  width: 100%;
  height:280px;
}


.add {
  padding: 0px 40px;
  width: 100%;
}




  .avatar-uploader{
    border: 2px dashed #d9d9d9;
    // border: 1px dashed yellowgreen;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    height: 178px;
  }
  .avatar-uploader:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>




